import React, { Component } from 'react';
import { Icon, Form, Button } from 'antd';
import { Btn } from 'components/common/button';
import './style.scss';

const ButtonGroup = Button.Group;
@Form.create({ name: 'formSerchIndividual' })
class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);

    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <ButtonGroup>
        <Btn>
          <Icon type="plus-square" theme="twoTone" />
          <a href="#create-subject/new">To k ng k</a>
        </Btn>
        <Btn>
          <Icon type="save" theme="twoTone" twoToneColor="#6699ff" />
          Lu mn TC
        </Btn>
        <Btn>
          <Icon type="file-add" theme="twoTone" twoToneColor="red" />
          Thm phm vi ng k
        </Btn>
      </ButtonGroup>
    );
  }
}

export default MenuTabTop;



