import React, { Component, Fragment } from 'react';
import { Card, Row, Table, Button, Col, Icon, Form, Select, Radio, Input } from 'antd';
import { CartHeadAction } from 'components/common';
import Page from 'components/LayoutComponents/Page';
import { CreateSubject, CreateSubject2 } from 'dump/data';
import MenuTabTop from './MenuTabTop';
import './style.scss';

const RadioGroup = Radio.Group;
const { Option } = Select;
@Form.create({ name: 'CreateSubject' })
class CreateSubjectTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 1,
    };

    this.columns = [
      {
        title: 'Kha/M',
        dataIndex: 'code',
        key: 'code',
        render: () => <Icon type="lock" />,
      },
      {
        title: 'K hiu tn lp TC',
        dataIndex: 'KH',
        key: 'KH',
      },
      {
        title: 'Tn mn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'S TC',
        dataIndex: 'STC',
        key: 'STC',
      },
      {
        title: 'H s',
        dataIndex: 'HS',
        key: 'HS',
      },
      {
        title: 'S lp',
        dataIndex: 'SL',
        key: 'SL',
      },
      {
        title: 'S SV ti a',
        dataIndex: 'Svmax',
        key: 'Svmax',
      },
      {
        title: 'S SV ti thiu',
        dataIndex: 'Svmin',
        key: 'Svmin',
      },
      {
        title: 'S SV .k',
        dataIndex: 'SvDK',
        key: 'SvDK',
      },
      {
        title: 'T.s tit',
        dataIndex: 'TST',
        key: 'TST',
      },
      {
        title: 'S.tit LT',
        dataIndex: 'STLT',
        key: 'STLT',
      },
      {
        title: 'S.tit TH',
        dataIndex: 'STTH',
        key: 'STTH',
      },
      {
        title: 'S.tit BT',
        dataIndex: 'STBT',
        key: 'STBT',
      },
      {
        title: 'S.tit TL',
        dataIndex: 'STTL',
        key: 'STTL',
      },
      // {
      //   title: 'Thao tc',
      //   dataIndex: 'operation',
      //   render: (text, record) =>
      //     listStudents.length >= 1 ? (
      //       <Fragment>
      //         <BtnEdit href="#/profile-student" />
      //         <Popconfirm title="Bn c mun xa?" onConfirm={() => this.handleDelete(record.key)}>
      //           <BtnDelete />
      //         </Popconfirm>
      //       </Fragment>
      //     ) : null,
      // },
    ];

    this.columns2 = [
      {
        title: 'Lp TC',
        dataIndex: 'LTC',
        key: 'LTC',
      },
      {
        title: 'Ti thiu',
        dataIndex: 'TIT',
        key: 'TIT',
      },
      {
        title: 'Ti a',
        dataIndex: 'TD',
        key: 'TD',
      },
      {
        title: 'S SV .k',
        dataIndex: 'SSV',
        key: 'SSV',
      },
      {
        title: 'S tit tun',
        dataIndex: 'STT',
        key: 'STT',
      },
      {
        title: 'Th',
        dataIndex: 'TH',
        key: 'TH',
      },
      {
        title: 'T tit',
        dataIndex: 'TT',
        key: 'TT',
      },
      {
        title: 'n tit',
        dataIndex: 'DT',
        key: 'DT',
      },
      {
        title: 'Ca hc',
        dataIndex: 'CH',
        // key: 'CH',
        render: (text, record) => (
          <Fragment>
            <Select defaultValue={record.CH} style={{ width: 120 }}>
              <Option value="1">Ca sng</Option>
              <Option value="2">Ca chiu</Option>
              <Option value="3">Ca ti</Option>
            </Select>
          </Fragment>
        ),
      },
      {
        title: 'Phng hc',
        dataIndex: 'PH',
        key: 'PH',
        render: (text, record) => (
          <Fragment>
            <Select defaultValue={record.PH} style={{ width: 120 }}>
              <Option value="1">D3-dy A</Option>
              <Option value="2">D4-dy B</Option>
              <Option value="3">D4-dy A</Option>
            </Select>
          </Fragment>
        ),
      },
      {
        title: 'Gio vin',
        dataIndex: 'GV',
        // width: 300,
        key: 'GV',
        render: (text, record) => (
          <Fragment>
            <Select defaultValue={record.GV} style={{ width: 200 }}>
              <Option value="1">Nguyn th A</Option>
              <Option value="2">Trn vn B</Option>
              <Option value="3">L th C</Option>
            </Select>
          </Fragment>
        ),
      },
      {
        title: 'T tun',
        dataIndex: 'TUT',
        key: 'TUT',
      },
      {
        title: 'n tun',
        dataIndex: 'DNT',
        key: 'DNT',
      },
      {
        title: 'T ngy',
        dataIndex: 'TN',
        key: 'TN',
      },
      {
        title: 'n ngy',
        dataIndex: 'DN',
        key: 'DN',
      },
    ];
    // rowSelection object indicates the need for row selection
  }

  // eslint-disable-next-line react/sort-comp
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  onChange = e => {
    console.log('radio checked', e.target.value);
    this.setState({
      value: e.target.value,
    });
  };

  render() {
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    const { getFieldDecorator } = this.props.form;

    return (
      <Card title={this.title}>
        <Row style={this.cssBetweenRow}>
          <Col span={24}>
            <CartHeadAction>
              <MenuTabTop />
            </CartHeadAction>
          </Col>
        </Row>
        <Form layout="vertical" className="ant-advanced-search-form" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <Col span={6}>
              <Form.Item label="H">
                {getFieldDecorator('system1')(
                  <Select>
                    <Option value="1">i hc chnh quy</Option>
                    <Option value="2">Cao ng chnh quy</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Khoa">
                {getFieldDecorator('system2', {})(
                  <Select>
                    <Option value="1">C-in t</Option>
                    <Option value="2">Qun tr kinh doanh</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Kha">
                {getFieldDecorator('system3', {})(
                  <Select>
                    <Option value="1">Kha 1</Option>
                    <Option value="2">Kha 2</Option>
                    <Option value="3">Kha 3</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Ngnh">
                {getFieldDecorator('system4', {})(
                  <Select>
                    <Option value="1">C kh</Option>
                    <Option value="2">in t vin thng</Option>
                    <Option value="3">in cng nghip</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Chuyn ngnh">
                {getFieldDecorator('system5', {})(
                  <Select>
                    <Option value="1">Qun tr mng</Option>
                    <Option value="2">Lp trnh web</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="K ng k">
                {getFieldDecorator('system6', {})(
                  <Select>
                    <Option value="1">K 1</Option>
                    <Option value="2">K 2</Option>
                    <Option value="2">K 3</Option>
                    <Option value="2">K 4</Option>
                    <Option value="2">K 5</Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="K hiu mn">{getFieldDecorator('system7')(<Input />)}</Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label=" ">
                {getFieldDecorator('system8')(
                  <RadioGroup onChange={this.onChange} value={this.state.value}>
                    <Radio value={1}>Tt c cc mn</Radio>
                    <Radio value={2}>Mn chung</Radio>
                  </RadioGroup>
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 1200 }}
              rowSelection={rowSelection}
              bordered
              dataSource={CreateSubject}
              columns={this.columns}
            />
          </Col>
        </Row>
        <Row style={{ marginBottom: 10 }}>
          <Col span={24}>
            <Button style={{ marginRight: 5 }} icon="save">
              Lu TKB
            </Button>
            <Button style={{ marginRight: 5 }} icon="plus">
              Thm lp
            </Button>
            <Button style={{ marginRight: 5 }} icon="close">
              Xa lp
            </Button>
            <Button style={{ marginRight: 5 }} icon="plus">
              Thm ca
            </Button>
            <label style={{ margin: 8 }}>B mn</label>
            <Select style={{ width: 180 }}>
              <Option value="1">Ting Anh</Option>
              <Option value="2">Ha</Option>
              <Option value="3">Ton</Option>
            </Select>
            <label style={{ margin: 8 }}>Ta nh</label>
            <Select style={{ width: 180 }}>
              <Option value="1">Ta nh 1</Option>
              <Option value="2">Ta nh 2</Option>
              <Option value="3">Ta nh 3</Option>
            </Select>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              scroll={{ x: 1200 }}
              rowSelection={rowSelection}
              bordered
              dataSource={CreateSubject2}
              columns={this.columns2}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreateSubjectTable />
  </Page>
);



